/* src/Game.css */
.game {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #333; /* สีพื้นหลังของหน้าจอ */
  color: #f5f5f5; /* สีข้อความ */
  font-family: 'Arial', sans-serif;
  padding: 20px; /* เพิ่ม padding เพื่อให้เนื้อหาไม่ติดขอบหน้าจอ */
  box-sizing: border-box;
}

.game-board {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px; /* เพิ่มช่องว่างระหว่างแถว */
}

/* src/App.css */
.square {
  width: 150px;
  height: 150px;
  background: #444; /* Background color of the button */
  border: 2px solid #666; /* Border color of the button */
  color: #fff; /* Default text color */
  font-size: 2.5rem; /* Font size */
  font-weight: bold;
  line-height: 100px; /* Centering text vertically */
  text-align: center;
  cursor: pointer;
  outline: none;
  transition: background-color 0.3s, border-color 0.3s;
}

.square:hover {
  background-color: #555; /* Hover background color */
  border-color: #777; /* Hover border color */
}

/* Style specifically for X */
.square.x {
  font-size: 5rem;
  color: #ff0000; /* Red color for X */
}

/* Style specifically for O */
.square.o {
  font-size: 5rem;
  color: #007bff; /* Blue color for O */
}

/* Rest of your styles... */


.board-row {
  display: flex;
}

.status {
  text-align: center;
  font-size: 1.5rem; /* ใช้ rem เพื่อให้ขนาดข้อความปรับตามขนาดของหน้าจอ */
  margin-bottom: 10px;
}

.reset-button {
  padding: 12px 24px;
  font-size: 1.5rem; /* ใช้ rem เพื่อให้ขนาดตัวอักษรปรับตามขนาดของหน้าจอ */
  font-weight: bold;
  color: #fff;
  background-color: #007bff; /* สีพื้นหลังของปุ่ม */
  border: none;
  border-radius: 8px; /* มุมปุ่มมน */
  cursor: pointer;
  outline: none;
  transition: background-color 0.3s, transform 0.3s, box-shadow 0.3s;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* เงาของปุ่ม */
}

.reset-button:hover {
  background-color: #0056b3; /* สีพื้นหลังเมื่อเอาเมาส์ไปชี้ */
  transform: translateY(-2px); /* เลื่อนปุ่มขึ้นเล็กน้อย */
}

.reset-button:active {
  background-color: #004494; /* สีพื้นหลังเมื่อกดปุ่ม */
  transform: translateY(0); /* ปลดการเลื่อนปุ่มขึ้น */
}

/* src/Game.css */
.mode-selector {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #333;
  color: #f5f5f5;
}

.mode-selector h1 {
  margin-bottom: 20px;
}

.mode-button {
  padding: 12px 24px;
  font-size: 1.5rem;
  font-weight: bold;
  color: #fff;
  background-color: #007bff;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  outline: none;
  transition: background-color 0.3s, transform 0.3s;
  margin: 10px;
}

.mode-button:hover {
  background-color: #0056b3;
  transform: translateY(-2px);
}

.mode-button:active {
  background-color: #004494;
  transform: translateY(0);
}


